@import url('https://fonts.googleapis.com/css?family=Righteous&display=swap');

body{
  font-family: 'Righteous', cursive;
  color: #d14f4f;
  line-height: 1.7;
  font-size: 19px;
  background-color: #222222;

}
.navbar{
  background-color:rgba(22, 22, 22, 0.8);
  display: flex;
  justify-content: center;
}


.Link{
  color: #efefef;
  display: inline-block;
  padding: 3px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: 0.3s ease all;
}

.Link:hover{
  color: #f59494;
  transform: scale(1.1);
}

.nasa-photo{
  display: flex;
  padding: 40px;
  margin: 0 auto;
  max-height: 100%;
}

.nasa-photo img{
  width: 50%;
  margin-right: 50px;
  object-fit: contain;
  max-height: 80vh;
}

.nasa-photo h1{
  margin-top: 0;
  margin-bottom: 0;
  display: inline;
}

.nasa-photo .date{
  margin-top: 0 ;
  margin-bottom: 30px;
}
.nasa-photo .explanation{
  color: #bbb;
}
@media (max-width: 1280px){
  .nasa-photo {
    display:block;
  }

  .nasa-photo img{
    width: 60%;
    margin-right: 0;
    margin-bottom: 30px;
  }
}


.home {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.home-link {
  font-size: 40px;
  background: #f59494;
  display: inline-block;
  padding: 40px 80px;
  color: #fff;
  border-radius: 50%;
  text-decoration: none;
  transition: 0.3s ease all;
  text-transform: uppercase;
}


.home-link:hover{
  border-radius: 5px;
  background:#e77 ;
}